  background: #ffffff;
  height: 170px;
}
.page-me .top .top-a .box1 {
  width: 100%;
  height: 20px;
  padding: 20px;
  float: left;
  text-align: center;
}
.page-me .top .top-a .box1 .order {
  font-weight: 500;
  font-size: 20px;
  color: #2e2e2e;
  float: left;
}
.page-me .top .top-a .box1 a {
  font-size: 14px;
  color: #646567;
  padding: 2px 28px;
  float: right;
}
.page-me .top .top-a .box1 a:active {
  color: black;
}
.page-me .top .top-a .box2 {
  float: left;
  color: #ffffff;
}
.page-me .top .top-a .box2 div {
  display: inline-block;
}
.page-me .top .top-a .box2 div .icon {
  margin-top: 25px;
  width: 19.8vw;
  display: block;
  text-align: center;
  color: #666;
  font-size: 30px;
}
.page-me .top .top-a .box2 div .a {
  color: #646567;
  font-size: 16px;
  display: block;
  text-align: center;
  margin-top: 8px;
}
.page-me .top .top-a .box3 {
  float: left;
  width: 100%;
  height: 12vw;
  margin-top: 40px;
  background: #ffffff;
}
.page-me .top .top-a .box3 .order {
  font-weight: 500;
  margin-top: 12px;
  margin-left: 15px;
  font-size: 18px;
  float: left;
}
.page-me .top .top-a .box3 a {
  font-size: 14px;
  color: #e02e24;
  margin-top: 12px;
  padding: 0 18px;
  float: right;
}
.page-me .top .top-a .box3 a .red {
  font-size: 20px;
  padding-right: 12px;
}
.page-me .top .top-a .box3 a span {
  display: inline-block;
}
.page-me .top .top-a .box3 a:hover {
  color: #666;
}
.page-me .top .top-a .box4 {
  float: left;
  width: 100%;
  height: 25vw;
  margin-top: 15px;
  background: #ffffff;
}
.page-me .top .top-a .box4 div {
  display: inline-block;
}
.page-me .top .top-a .box4 div .icon {
  margin-top: 18px;
  width: 19.9vw;
  display: block;
  text-align: center;
  color: #e02e24;
  font-size: 30px;
}
.page-me .top .top-a .box4 div .a {
  color: #646567;
  font-size: 14px;
  display: block;
  text-align: center;
  margin-top: 8px;
}
.page-me .top .top-a .box5 {
  float: left;
  width: 100%;
  height: 45vw;
  margin-top: 15px;
  background: #ffffff;
}
.page-me .top .top-a .box5 div {
  display: inline-block;
}
.page-me .top .top-a .box5 div .icon {
  margin: 15px 0;
  width: 19.9vw;
  display: block;
  text-align: center;
  color: #f0a15e;
  font-size: 30px;
}
.page-me .top .top-a .box5 div .a {
  font-size: 14px;
  display: block;
  color: #646567;
  text-align: center;
  margin-top: 8px;
}
.page-me .top .top-a .box6 {
  float: left;
  background: #ffffff;
}
.page-me .top .top-a .box6 .frame1 {
  height: 50vw;
  width: 48vw;
  float: left;
  background-color: #666;
}
.page-me .top .top-a .box6 .frame2 {
  height: 50vw;
  width: 48vw;
  margin-left: 2px;
  float: left;
  background-color: #666;
}
.page-me .top .top-a .box6 .text:hover {
  border: solid 1px red;
}
