// Module
___CSS_LOADER_EXPORT___.push([module.id, `.page.cart {
  min-height: 100vh;
  background: #f7f7f7;
}
.page.cart .item {
  background: #fff;
  margin-top: 20px;
  position: relative;
  height: 120px;
  border-radius: 5px;
  padding-left: 132px;
  padding-top: 10px;
  padding-right: 10px;
  box-sizing: border-box;
}
.page.cart .item .select {
  position: absolute;
  left: 8px;
  top: 50%;
  transform: translateY(-50%);
}
.page.cart .item .pic {
  width: 100px;
  height: 100px;
  position: absolute;
  left: 26px;
  top: 10px;
  background-size: cover;
  background-position: center center;
}
.page.cart .item .title {
  font-size: 16px;
  line-height: 24px;
  height: 24px;
  overflow: hidden;
}
.page.cart .item .sku {
  color: #888;
}
.page.cart .item .price {
  margin-top: 6px;
  font-size: 16px;
  color: #f30;
}
.page.cart .item .price span {
  font-size: 22px;
}
.page.cart .item .count {
  width: 120px;
  position: absolute;
  right: 10px;
  bottom: 20px;
  text-align: center;
}
.page.cart .total {
  height: 60px;
}
.page.cart .total .box {
  position: fixed;
  height: 60px;
  background: #fff;
  left: 0;
  right: 0;
  bottom: 61px;
  line-height: 60px;
  padding: 0 10px;
}
.page.cart .total .box .right {
  float: right;
}
.page.cart .total .box .total-price {
  color: #f30;
  font-size: 16px;
}
.page.cart .total .box .total-price .span {
  font-size: 22px;
}
.page.cart .total .box button {
  height: 40px;
  line-height: 40px;
  width: 90px;
  border-radius: 40px;
  border: none;
  transform: translateY(-3px);
  margin-left: 10px;
  background: #fa541c;
  color: #fff;
}
