  height: 60px;
}
.navigation .wrap {
  position: fixed;
  height: 60px;
  z-index: 100;
  bottom: 0;
  width: 100vw;
  left: 0;
  background: #f7f7f7;
  border-top: solid 1px #ccc;
}
.navigation .nav {
  width: 25vw;
  display: inline-block;
  text-align: center;
  padding-top: 10px;
  color: #333;
  text-decoration: none;
}
.navigation .nav .icon {
  font-size: 20px;
  margin-bottom: 5px;
}
.navigation .nav span {
  display: block;
  font-size: 11px;
}
.navigation .nav:hover {
  color: #f30;
}
.navigation .nav.active {
  background: #f30;
  color: #fff;
}
