___CSS_LOADER_EXPORT___.push([module.id, `.page.index {
  min-height: 100vh;
  background: #f7f7f7;
}
.page.index .slider .carousel .img {
  width: 100vw;
  height: 55vw;
  background-position: center center;
  background-size: cover;
}
.page.index .slider .carousel .slick-dots button {
  background-color: rgba(0, 0, 0, 0.7);
}
.page.index .slider .carousel .slick-active button {
  background: #f30;
}
.page.index .module {
  margin-top: 10px ;
}
.page.index .module > .title {
  font-size: 22px;
  text-align: center;
  line-height: 50px;
  position: relative;
}
.page.index .module > .title span {
  display: inline-block;
  padding: 0 20px;
  background: #f7f7f7;
  position: relative;
  z-index: 10px;
}
.page.index .module > .title:before {
  content: '';
  display: block;
  position: absolute;
  height: 1px;
  top: 25px;
  left: 20vw;
  width: 60vw;
  background: #ccc;
}
.page.index .module .items {
  overflow: hidden;
}
.page.index .module .item {
  width: 45vw;
  background: #fff;
  margin-left: 3.33vw;
  height: calc(45vw + 100px);
  margin-bottom: 20px;
  float: left;
  color: #333;
  text-decoration: none ;
}
.page.index .module .item img {
  display: block;
  width: 45vw;
  height: 45vw;
  object-fit: cover;
}
.page.index .module .item .title {
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  height: 48px;
  overflow: hidden;
  margin-top: 6px;
}
.page.index .module .item .price-box {
  text-align: center;
  margin-top: 3px;
}
.page.index .module .item .price-box div {
  display: inline-block;
}
.page.index .module .item .price-box .price {
  font-size: 16px;
  color: #f30;
}
.page.index .module .item .price-box .price span {
  font-size: 22px;
}
.page.index .module .item .price-box .origin-price {
  font-size: 13px;
  text-decoration: line-through;
  margin-top: 10px;
}
