___CSS_LOADER_EXPORT___.push([module.id, `.page.item {
  background: #f7f7f7;
  min-height: 100vh;
}
.page.item .slider .carousel .img {
  width: 100vw;
  height: 100vw;
  background-position: center center;
  background-size: cover;
}
.page.item .slider .carousel .slick-dots button {
  background-color: rgba(0, 0, 0, 0.7);
}
.page.item .slider .carousel .slick-active button {
  background: #f30;
}
.page.item .info {
  margin-top: 12px;
  background: #fff;
  padding: 10px 20px;
}
.page.item .info .title {
  font-size: 16px;
  line-height: 24px;
  max-height: 48px;
  overflow: hidden;
}
.page.item .info .price-box div {
  display: inline-block;
}
.page.item .info .price-box .price {
  font-size: 16px;
  color: #f30;
}
.page.item .info .price-box .price span {
  font-size: 22px;
}
.page.item .info .price-box .origin-price {
  margin-left: 10px;
  text-decoration: line-through;
  color: #666;
  font-size: 13px;
}
.page.item .detail {
  width: 100vw;
  overflow: hidden;
}
.page.item .detail img {
  display: block;
  width: 100%;
}
.page.item .operation {
  height: 80px;
}
.page.item .operation .wrap {
  position: fixed ;
  bottom: 0;
  height: 60px;
  border-top: solid 1px #ddd;
  left: 0;
  width: 100vw;
  background: #fff;
  z-index: 99;
}
.page.item .operation .wrap div {
  margin-left: 7vw;
  width: 40vw;
  display: inline-block;
  margin-top: 10px;
}
.page.item .operation .add-cart,
.page.item .operation .buy {
  height: 40px;
  text-align: center;
  line-height: 40px;
  margin-top: 10px;
  color: #fff;
  display: block;
  border-radius: 50px;
  width: 80vw;
  margin: 20px auto;
}
.page.item .operation .add-cart {
  background: #ffc53d;
}
.page.item .operation .buy {
  background: #f5222d;
}
.page.item .sku-select {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100vw;
  z-index: 200;
  background: #fff;
  border-top: solid 1px #ccc;
  padding: 20px;
  box-sizing: border-box;
}
.page.item .sku-select .sku .title {
  font-size: 20px;
  margin-top: 20px;
}
.page.item .sku-select .sku .box {
  overflow: hidden;
}
.page.item .sku-select .sku .box .option {
  width: 35vw;
  text-align: center;
  line-height: 40px;
  font-size: 16px;
  border: solid 1px #ddd;
  border-radius: 10px;
  float: left;
  margin-top: 12px;
  margin-right: 20px;
}
.page.item .sku-select .sku .box .option.active {
  color: #f30;
  border-color: #f30;
}
.page.item .mask {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
}
.page.item .sku-select-wrap .mask {
  display: none;
}
.page.item .sku-select-wrap .sku-select {
  transition: transform 0.2s;
  transform: translateY(100%);
}
.page.item .sku-select-wrap.show .mask {
  display: none;
}
.page.item .sku-select-wrap.show .sku-select {
  transform: translateY(0);
}
